.section__title--compare {
  color: $color-white;
  font-size: 32px;
  line-height: 32px;
  letter-spacing: -1.5px;
  text-align: center;
  font-weight: bold;
  width: 100%;

  @include onTablet {
    font-size: 48px;
    line-height: 48px;
  }

  @include onDesktop {
    font-size: 64px;
    line-height: 64px;
  }
}
