.contact-us {
  &__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 24px;
    color: $color-white;

    @include onTablet {
      grid-template-columns: repeat(6, 1fr);
      column-gap: 24px;
    }

    @include onDesktop {
      grid-template-columns: repeat(12, 1fr);
      column-gap: 24px;
    }
  }

  &__input {
    padding-left: 16px;
    box-sizing: border-box;
    border: none;
    width: 100%;
    height: 48px;
    border-radius: 40px;
    grid-column: 2 / 3;
    margin: 16px 0;
    background: #f8f8fa;

    &::placeholder {
      font-size: 14px;
      line-height: 20px;
      color: #7e7e83;
    }

    &:hover {
      border-color: #d5d7de;
    }

    &:focus {
      border-color: #292929;
      color: #292929;
      outline: none;
    }

    @include onDesktop {
      grid-column: 1 / 6;
    }
  }

  &__textarea {
    box-sizing: border-box;
    background: #f8f8fa;
    margin-top: 16px;
    resize: none;
    width: 100%;
    height: 148px;
    border-radius: 16px;
    font-family: inherit;
    padding: 14px 16px;

    &::placeholder {
      line-height: 20px;
      font-size: 14px;
      color: #7e7e83;
    }

    &:hover {
      border-color: #d5d7de;
    }

    &:focus {
      border-color: #292929;
      color: #292929;
      outline: none;
    }

    @include onTablet {
      grid-column: 1 / 3;
    }

    @include onDesktop {
      grid-column: 1 / 5;
    }
  }

  &__button {
    color: $color-main;
    background-color: #f8f8fa;
    margin-top: 32px;
    width: 100%;
    height: 48px;
    font-size: 16px;
    line-height: 27px;
    font-weight: 700;
    border-radius: 40px;
    grid-column: 1 / 2;
    cursor: pointer;
    transition: box-shadow $transitionTime;

    &:hover {
      box-shadow: 1px 4px 16px 4px rgba(232, 232, 240, 0.4);
    }

    &:focus {
      box-shadow: 1px 4px 16px 0 rgba(232, 232, 240, 0.64);
    }
  }

  &__phone {
    font-size: 14px;
    line-height: 20px;
    padding-top: 20px;
    color: $color-white;
  }

  &__tel {
    font-size: 14px;
    line-height: 20px;
    padding-top: 8px;
    color: $color-white;
    width: max-content;
  }

  &__link {
    font-size: 16px;
    line-height: 22px;
    text-decoration: none;
    color: $color-white;
    padding-bottom: 24px;
    transition-duration: 0.5s;

    &:hover {
      color: $color-links;
    }
  }

  &__email {
    font-size: 14px;
    line-height: 20px;
    color: $color-white;
    padding-top: 24px;
  }

  &__link--email {
    font-size: 16px;
    line-height: 22px;
    text-decoration: none;
    color: $color-white;
    padding-top: 8px;
  }

  &__address {
    font-size: 14px;
    line-height: 20px;
    color: $color-white;
    padding-top: 24px;
    padding-bottom: 8px;
  }

  &__end {
    width: max-content;
  }

  &__form {
    grid-column: 1 / 3;

    @include onTablet {
      grid-column: 1 / 4;
    }

    @include onDesktop {
      grid-column: 1 / 6;
    }
  }

  &__info {
    grid-column: 1 / 3;

    @include onTablet {
      grid-column: 4 / 7;
    }

    @include onDesktop {
      grid-column: 8 / 12;
    }
  }
}
